.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  padding: 0;
  margin: 0;
}

.board {
  width: 500px;
  height: 500px;
  background-color: rgb(244, 127, 255);
  border: 1px solid rgb(13, 250, 250);
  display: flex;
  flex-direction: column;
}

.row {
  flex: 33%;
  display: flex;
  flex-direction: row;
}

.square {
  flex: 33%;
  height: 100%;
  border: 2px solid black;
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: 40px;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}

.square:active {
  background-color: #64f4c4;
}
